html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5, h6, p, blockquote, pre, a, abbr, acronym, address, big, cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small, strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt, dd, ol, ul, li, fieldset, form, label, legend, table, caption, tbody, tfoot, thead, tr, th, td, article, aside, canvas, details, embed, figure, figcaption, footer, header, hgroup, menu, nav, output, ruby, section, summary, time, mark, audio, video {
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
  border: none;
  margin: 0;
  padding: 0;
}

article, aside, details, figcaption, figure, footer, header, hgroup, menu, nav, section {
  display: block;
}

body {
  line-height: 1;
}

ol, ul {
  list-style: none;
}

blockquote, q {
  quotes: none;
}

blockquote:before, blockquote:after, q:before, q:after {
  content: "";
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

.bd__button {
  text-align: right;
  padding: 10px 0;
}

.bd__button > a {
  color: #bdc3cb;
  font-family: sans-serif;
  font-weight: 100;
  text-decoration: none;
}

.bd__button > a:hover {
  color: #798897;
}

footer {
  width: 100%;
  min-height: 100%;
  display: block;
  position: relative;
  bottom: 0;
}

footer.icon {
  color: "black";
}

.background {
  min-height: 100%;
  width: 100%;
  pointer-events: none;
  background-position: center;
  background-size: cover;
  position: absolute;
}

.chatbot {
  width: 100%;
  height: 100vh;
  justify-content: center;
  align-items: center;
  font-size: 18px;
}

.chat-wrapper {
  clear: both;
  height: 80vh;
  flex-direction: column;
  display: flex;
}

.message-container {
  text-align: center;
  flex-grow: 2;
  padding-top: .2rem;
  padding-bottom: .2rem;
  overflow-y: auto;
  border-bottom: none !important;
}

.message-bubble {
  text-align: left;
  max-width: 70%;
  border-radius: .5rem;
  flex-shrink: 0;
  padding: .5rem;
  display: inline-block;
  position: relative;
}

.message-row {
  clear: both;
  margin: .75rem;
  display: flex;
}

.message-row[data-origin="system"] + .message-row:not([data-origin="system"]), .message-row:not([data-origin="system"]) + .message-row[data-origin="system"] {
  margin-top: .3rem;
}

.message-row .before, .message-row .after {
  min-width: 1%;
}

.message-row[data-origin="sending"] {
  opacity: .75;
}

.message-row[data-origin="sending"] .before, .message-row[data-origin="sent"] .before, .message-row[data-origin="received"] .after, .message-row[data-origin="system"] .before, .message-row[data-origin="system"] .after {
  min-width: 10%;
  flex-grow: 2;
}

.message-row:hover .reply-btn {
  vertical-align: middle;
  justify-content: center;
  align-items: center;
  display: inline-flex;
}

.message-row:not(:hover) .reply-btn {
  display: none;
}

.message-bubble .message-text {
  display: inline;
}

.message-bubble .timestamp {
  display: inline-block;
}

.message-bubble .timestamp-invisible-space {
  visibility: hidden;
  margin-left: 1em;
  display: inline-block;
}

.message-bubble .timestamp-invisible-space, .message-bubble .timestamp-visible {
  font-size: 70%;
}

.message-bubble .timestamp-visible {
  -webkit-user-select: none;
  user-select: none;
  opacity: .5;
  position: absolute;
  bottom: .5em;
  right: .5em;
}

.message-sender {
  opacity: .7;
  color: #000;
  margin-bottom: .3em;
  font-size: 70%;
  font-weight: bold;
}

.message-text {
  overflow-wrap: anywhere;
  white-space: pre-line;
}

.after-bubbles {
  clear: both;
  margin-bottom: 1em;
}

.chat-header {
  flex-shrink: 0;
}

.chat-header-card {
  border-radius: 15px 15px 0 0;
  flex-shrink: 0;
}

.quoted-message {
  color: #000;
  background-color: #ffffffb3;
  border: 1px solid #0000001f;
  padding: .5em;
  font-size: 80%;
  display: flex;
}

.message-row .quoted-message {
  margin-bottom: .5em;
}

.quoted-message .main-quote {
  flex-grow: 2;
}

.quoted-message .left-quote {
  width: 4px;
  background-color: gray;
  flex-grow: 0;
  flex-shrink: 0;
  margin-right: 3px;
}

.chatbot-container {
  background-color: #00f;
  border-radius: 25px;
  margin-top: 2em;
  padding-bottom: 1em;
}

.chatbot-container-header {
  width: 100%;
}

.chatbot-return-button {
  width: 10%;
  height: 100%;
  color: #fff;
}

.App-body {
  min-height: 100vh;
  font-size: calc(10px + 2vmin);
  display: flex;
  position: relative;
  overflow: hidden;
}

.initial {
  width: 100%;
  height: 100%;
  justify-content: center;
  align-items: center;
}

.title {
  color: #ffffffe6;
  width: 100%;
  z-index: 1;
  flex-flow: column wrap;
  justify-content: center;
  align-items: center;
  position: relative;
}

.grid {
  -webkit-backdrop-filter: blur(2.6px);
  backdrop-filter: blur(2.6px);
  background: #ffffff26;
  border: 1px solid #ffffff24;
  border-radius: 16px;
  justify-content: center;
  align-items: center;
  margin: auto auto 80px;
  position: relative;
  box-shadow: 0 4px 30px #0000001a;
}

.root {
  transition: transform .15s ease-in-out;
}

.cardhovered {
  transform: scale3d(1.05, 1.05, 1);
}

.reporter {
  width: 100%;
  height: 100vh;
  justify-content: center;
  align-items: center;
  font-size: 20px;
  position: absolute;
}

.reporter-container {
  min-width: 40vw;
  height: 90vh;
  background-color: #00f;
  border-radius: 25px;
  margin-top: 2em;
  padding-bottom: 1em;
  position: relative;
}

.reporter-container-header {
  width: 100%;
}

.reporter-return-button {
  width: 10%;
  height: 100%;
  color: #fff;
}

.twitter-follow-button, .twitter-timeline {
  clear: both;
}

.wiki {
  width: 100%;
  height: 100vh;
  justify-content: center;
  align-items: center;
  font-size: 20px;
  position: absolute;
}

.wiki-container {
  min-width: 80vw;
  min-height: 80vh;
  background-color: #00f;
  border-radius: 25px;
  margin-top: 2em;
  padding-bottom: 1em;
  position: relative;
}

.wiki-container-header {
  width: 100%;
}

.wiki-return-button {
  width: 10%;
  height: 100%;
  color: #fff;
}

.wiki-iframe {
  width: 75vw;
  height: 80vh;
}

